.result {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  padding: 15rem 5.7rem 6rem;
  width: 100%;

  @media (min-width: $stop-scaling) {
    padding: 150px 57px 60px;
  }

  @media (orientation: portrait) {
    padding: 8rem 0 4rem;
  }

  @media (max-width: $tablet) and (orientation: landscape) {
    padding: 8rem 0 3rem;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    padding: 3rem 0;
  }
}

.result__image {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: -1;

  @media (orientation: portrait) {
    left: -1rem;
    right: -1rem;
  }

  picture {
    display: block;
    width: 100%;
    height: 100%;
  }

  img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }

  .flamingo {
    position: absolute;
    bottom: 13%;
    max-width: 300px;
    height: auto;
    z-index: 1;
    transform-origin: 50% 60%;
    animation: .8s ease-out 0s 1 normal both resultFlamingoFadeIn,
    1s ease-in-out .9s infinite alternate forwards resultFlamingoFly;
  }
  .leaf-blue-right {
    position: absolute;
    bottom: 24%;
    max-width: 330px;
    height: auto;
    transform-origin: 0 100%;
    animation-name: resultLeafBlueFadeInRight;
    animation-fill-mode: both;
    animation-delay: .4s;
    animation-duration: 1.8s;
    animation-timing-function: ease-out;
  }
  .leaf-blue-left {
    position: absolute;
    bottom: 13%;
    max-width: 340px;
    height: auto;
    transform-origin: 100% 0;
    animation-name: resultLeafBlueFadeInLeft;
    animation-fill-mode: both;
    animation-delay: .4s;
    animation-duration: 2s;
    animation-timing-function: ease-out;
  }
  .leaf-green {
    position: absolute;
    bottom: 21%;
    transform: translateX(62%);
    max-width: 380px;
    height: auto;
    transform-origin: 0 100%;
    animation-name: resultLeafGreenFadeIn;
    animation-fill-mode: both;
    animation-delay: .6s;
    animation-duration: 1.8s;
    animation-timing-function: ease-out;
  }
  .suitcase {
    position: absolute;
    bottom: 11%;
    transform: rotate(-5deg);
    max-width: 430px;
    height: auto;
    animation-name: resultSuitcaseFadeIn;
    animation-fill-mode: both;
    animation-duration: .8s;
    animation-timing-function: cubic-bezier(0.25, 0.1, 0.61, 1.6);
  }
}

.result__title {
  margin: 0 auto 2rem;
  color: $c-light;

  @media (min-width: $stop-scaling) {
    margin: 0 auto 20px;
  }

  @media (orientation: portrait) {
    margin-bottom: 1.5rem;
  }

  svg {
    display: block;
    width: 100%;
    height: 100%;
    stroke-width: 0.2rem;
    stroke-linejoin: round;
  }
}

.result__text {
  margin-bottom: 2rem;
  text-align: center;
  font-family: $font-alt;
  font-weight: 400;
  font-size: 4.8rem;
  line-height: 0.906;
  letter-spacing: -0.01em;

  @media (min-width: $stop-scaling) {
    margin-bottom: 20px;
    font-size: 48px;
  }

  @media (orientation: portrait) {
    font-size: 2.8rem;
    line-height: 1.1;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    font-size: 2.8rem;
  }

  p {
    margin: 0;
  }
}

.result__button {
  @include btn-reset;
  display: flex;
  align-items: center;
  justify-content: center;
  align-self: center;
  margin: auto auto 1rem;
  min-width: 34rem;
  text-transform: uppercase;
  font-size: 2.4rem;
  line-height: 1.2;
  color: $c-light;

  @media (min-width: $stop-scaling) {
    margin: auto auto 10px;
    min-width: 340px;
    font-size: 24px;
  }

  @media (orientation: portrait) {
    margin: auto 0 0;
    transform: none;
    width: 100%;
    min-width: auto;
    font-size: 1.6rem;
  }

  @media (max-width: $tablet) and (orientation: landscape) {
    transform: none;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    margin: auto 0 0;
    width: 100%;
    min-width: auto;
    font-size: 1.6rem;
  }

  span {
    position: relative;
    margin-left: 1.5rem;
    width: 4rem;
    height: 4rem;
    color: $c-dark;

    @media (min-width: $stop-scaling) {
      margin-left: 15px;
      width: 40px;
      height: 40px;
    }

    &::before {
      @include center;
      content: "";
      width: 100%;
      height: 100%;
      background-color: $c-light;
      border: 2px solid $c-light;
      border-radius: 50%;
      z-index: -1;
    }
  }

  svg {
    @include center;
    margin-left: 0.3rem;
    width: 2.4rem;
    height: 1.9rem;

    @media (min-width: $stop-scaling) {
      margin-left: 3px;
      width: 24px;
      height: 19px;
    }
  }

  &:hover {
    span::before {
      width: 112.5%;
      height: 112.5%;
    }
  }

  &:focus {
    outline: none;

    span::before {
      border-color: $c-purple;
    }
  }

  &:active,
  &:active:focus {
    span {
      color: $c-light;
    }

    span::before {
      width: 100%;
      height: 100%;
      background-color: $c-purple;
      border-color: $c-purple;
    }
  }
}

.result__form {
  margin-top: auto;
}

.result--trip,
.result--prize {
  .result__title {
    width: 57.3rem;
    height: 11.3rem;

    @media (min-width: $stop-scaling) {
      width: 573px;
      height: 113px;
    }

    @media (orientation: portrait) {
      width: 28.7rem;
      height: 5.7rem;
    }

    @media (max-width: $tablet) and (orientation: landscape) {
      width: 40rem;
    }

    @media (max-width: $mobile) and (orientation: landscape) {
      margin-bottom: 0;
      width: 35rem;
    }
  }
}

.result--negative {
  .result__title {
    margin-top: 9.5rem;
    width: 66.5rem;
    height: 11.3rem;

    @media (min-width: $stop-scaling) {
      margin-top: 95px;
      width: 665px;
      height: 113px;
    }

    @media (orientation: portrait) {
      margin-top: 0;
      width: 28.8rem;
      height: 4.9rem;
    }

    @media (max-width: $tablet) and (orientation: landscape) {
      margin-top: 0;
      width: 50rem;
    }

    @media (max-width: $mobile) and (orientation: landscape) {
      width: 45rem;
    }
  }
}

@keyframes resultFlamingoFadeIn {
  0% {
    transform: translate(0, 80%) rotateZ(-40deg);
    opacity: 0;
  }
  100% {
    transform: translate(-30%, 0) rotateZ(0deg);
    opacity: 1;
  }
}
@keyframes resultSuitcaseFadeIn {
  0% {
    transform: rotate(-5deg) translate(0, 40%) scale(0);
    opacity: 0;
  }
  100% {
    transform: rotate(-5deg) translate(0, 0) scaleX(1);
    opacity: 1;
  }
}
@keyframes resultLeafBlueFadeInRight {
  0% {
    transform: translateX(62%) rotateZ(-30deg) scale(.6);
    opacity: 0;
  }
  14% {
    transform: translateX(62%) rotateZ(10deg);
    opacity: 1;
  }
  32% {
    transform: translateX(62%) rotateZ(-5deg) scale(1);
    opacity: 1;
  }
  55% {
    transform: translateX(62%) rotateZ(4deg) scale(1);
    opacity: 1;
  }
  80% {
    transform: translateX(62%) rotateZ(-2deg) scale(1);
    opacity: 1;
  }
  100% {
    transform: translateX(62%) rotateZ(0deg) scale(1);
    opacity: 1;
  }
}
@keyframes resultLeafBlueFadeInLeft {
  0% {
    transform: translateX(-65%) rotateZ(30deg) scale(.6);
    opacity: 0;
  }
  18% {
    transform: translateX(-65%) rotateZ(-10deg) scale(1);
    opacity: 1;
  }
  36% {
    transform: translateX(-65%) rotateZ(5deg) scale(1);
    opacity: 1;
  }
  60% {
    transform: translateX(-65%) rotateZ(-4deg) scale(1);
    opacity: 1;
  }
  85% {
    transform: translateX(-65%) rotateZ(2deg) scale(1);
    opacity: 1;
  }
  100% {
    transform: translateX(-65%) rotateZ(0deg) scale(1);
    opacity: 1;
  }
}
@keyframes resultLeafGreenFadeIn {
  0% {
    transform: translateX(62%) rotateZ(-30deg);
    opacity: 0;
  }
  16% {
    transform: translateX(62%) rotateZ(10deg);
    opacity: 1;
  }
  35% {
    transform: translateX(62%) rotateZ(-10deg);
    opacity: 1;
  }
  60% {
    transform: translateX(62%) rotateZ(8deg);
    opacity: 1;
  }
  85% {
    transform: translateX(62%) rotateZ(-5deg);
    opacity: 1;
  }
  100% {
    transform: translateX(62%) rotateZ(0deg);
    opacity: 1;
  }
}
@keyframes resultFlamingoFly {
  0% {
    transform: translate(-30%, 0) rotateZ(0deg);
  }
  100% {
    transform: translate(-30%, 5%) rotateZ(-4deg);
  }
}
